export const converdate = (originalDatetimeStr) => {
  // Convert the UTC string to a Date object
  var originalDatetime = moment.tz(originalDatetimeStr, 'GMT').tz('Asia/Shanghai');
  const localDatetimeStr = originalDatetime.format('MM/DD HH:mm');

  return localDatetimeStr;
}

export const converdate_tosec = (originalDatetimeStr) => {
  // Convert the UTC string to a Date object
  var originalDatetime = moment.tz(originalDatetimeStr, 'GMT').tz('Asia/Shanghai');
  const localDatetimeStr = originalDatetime.format('MM/DD HH:mm:ss');
    
  return localDatetimeStr;
}

export const converdate_utc = (originalDatetimeStr)=>{
  //Convert the string to a Date object
  //alert(originalDatetimeStr)// Convert the string to a Date object
  var originalDatetime = new Date(originalDatetimeStr);
  // Get month, day, hours, and minutes
  var month = ('0' + (originalDatetime.getMonth() + 1)).slice(-2);
  var day = ('0' + originalDatetime.getDate()).slice(-2);
  var hours = ('0' + originalDatetime.getHours()).slice(-2);
  var minutes = ('0' + originalDatetime.getMinutes()).slice(-2);

  // Formatted datetime string
  var formattedDatetimeStr = month + day + '-' + hours + ':' + minutes;
  return formattedDatetimeStr
}

export const convertToGMT = (dateTime) => {
  if (!dateTime) {
    return null;
  }
  return moment.utc(dateTime).format('YYYY-MM-DD HH:mm:ss');
};

export const formatTimeDifference = (timeDifference) => {
  const units = ['years', 'months', 'days', 'hours', 'minutes'];
  const singularUnits = ['year', 'month', 'day', 'hour', 'minute'];

  for (let i = 0; i < units.length; i++) {
    const unit = units[i];
    if (timeDifference[unit] !== 0) {
      if (unit === 'minutes') {
        return `${timeDifference[unit]} ${timeDifference[unit] === 1 ? singularUnits[i] : unit} 之前`;
      } else {
        return `${timeDifference[unit]} ${timeDifference[unit] === 1 ? singularUnits[i] : unit} 之前`;
      }
    }
  }

  return '刚才';
};

export const formatNumber = (number) => {
  if (number === null || number === undefined || number === 0) {
    return '0';
  }

  const suffixes = ['', 'k', 'M', 'B', 'T'];
  const magnitude = Math.floor(Math.log10(number) / 3);
  const scaledNumber = number / Math.pow(10, magnitude * 3);
  const suffix = suffixes[magnitude];

  if (scaledNumber % 1 === 0) {
    return scaledNumber.toFixed(0) + suffix;
  }

  return scaledNumber.toFixed(1) + suffix;
};

export const formatTime = (lastExecutionTime) => {
  try {
    const parsedTime = new Date(lastExecutionTime + 'Z'); // append 'Z' to indicate UTC
    if (isNaN(parsedTime.getTime())) {
      throw new Error('Invalid time value');
    }

    const timeZone = 'Asia/Kolkata';
    const zonedTime = utcToZonedTime(parsedTime, timeZone);

    return formatDistanceToNow(zonedTime, {
      addSuffix: true,
      includeSeconds: true
    }).replace(/about\s/, '')
      .replace(/minutes?/, 'min')
      .replace(/hours?/, 'hrs')
      .replace(/days?/, 'day')
      .replace(/weeks?/, 'week');
  } catch (error) {
    console.error('Error formatting time:', error);
    return 'Invalid Time';
  }
};

export const formatBytes = (bytes, decimals = 2) => {
  if (bytes === 0) {
    return '0 Bytes';
  }

  const k = 1024;
  const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
  const i = Math.floor(Math.log(bytes) / Math.log(k));
  const formattedValue = parseFloat((bytes / Math.pow(k, i)).toFixed(decimals));

  return `${formattedValue} ${sizes[i]}`;
};

export const downloadFile = (fileId, fileName = null) => {
  // const authToken = localStorage.getItem('accessToken');
  const authToken = Cookies.get("accessToken");
  const url = `${baseUrl()}/resources/get/${fileId}`;
  const env = localStorage.getItem('applicationEnvironment');

  if (env === 'PROD') {
    const headers = {
      Authorization: `Bearer ${authToken}`,
    };

    return fetch(url, {headers})
      .then((response) => response.blob())
      .then((blob) => {
        if (fileName) {
          const fileUrl = window.URL.createObjectURL(blob);
          const anchorElement = document.createElement('a');
          anchorElement.href = fileUrl;
          anchorElement.download = fileName;
          anchorElement.click();
          window.URL.revokeObjectURL(fileUrl);
        } else {
          return blob;
        }
      })
      .catch((error) => {
        console.error('Error downloading file:', error);
      });
  } else {
    if (fileName) {
      window.open(url, '_blank');
    } else {
      return fetch(url)
        .then((response) => response.blob())
        .catch((error) => {
          console.error('Error downloading file:', error);
        });
    }
  }
};



