export const createInternalId = () => {
  let newId = 1;

  if (typeof window !== 'undefined') {
    let idsArray = getInternalIds();
    let found = false;

    for (let i = 1; !found; i++) {
      if (!idsArray.includes(i)) {
        newId = i;
        found = true;
      }
    }

    idsArray.push(newId);
    localStorage.setItem('agi_internal_ids', JSON.stringify(idsArray));
  }

  return newId;
};

