export async function getFileUrl(fileId, autodelete=false) {
  const db = await openDatabase();
  const transaction = db.transaction('files', 'readwrite');
  const store = transaction.objectStore('files');
  const request = store.get(fileId);

  return new Promise((resolve, reject) => {
    request.onerror = () => {
      db.close();
      reject(request.error);
    };
    request.onsuccess = () => {
      const file = request.result ? request.result.file : null;
      if (file) {
        // Generate a new URL and associate it with the file content
        const url = URL.createObjectURL(file);
        
        // Delete the file from the database if autodelete is true
        if (autodelete) {
          //alert('deleting'+fileId)
          const deleteRequest = store.delete(fileId);
          deleteRequest.onsuccess = () => {
            db.close();
            resolve(null)
          };
          deleteRequest.onerror = () => {
            db.close();
            reject('Error deleting file from database');
          };
        } else {
          db.close();
          resolve(url);
        }        
      } else {
        // No file associated with the fileId or the file does not exist in the database
        db.close();
        resolve(null); // Return null when no file is found
      }
    };
  });
}


