export async function fetchBlob(url) {
  try {
    const response = await fetch(url);
    if (!response.ok) {
      throw new Error(`Failed to fetch blob from ${url}`);
    }
    return await response.blob();
  } catch (error) {
    console.error("Error downloading the file", error);
    return null;
  }
}

// 新函数，将远程URL转换为本地Blob URL
export async function getLocalResourceFromURL(value, option) {
  if (!value || !option) {
    return { url: null, file: null };
  }

  // 尝试从数据库中获取文件
  let file = await getFileObject(value);
  if (!file) {
    // 如果数据库中没有文件，从远程URL获取Blob
    const blob = await fetchBlob(option);
    if (!blob) {
      return { url: null, file: null };
    }
    file = new File([blob], value, { type: blob.type });
    // 保存文件到数据库
    const url = await saveFile(file, value);
    return { url, file };
  } else {
    // 如果文件已存在于数据库中，创建本地URL
    const url = URL.createObjectURL(file);
    return { url, file };
  }
}

