export function sortTemplateList(data){

  // Define the desired order
  const order = ['百变真我学习趣味爱情节日短视频电商音乐电商跳舞其他','百变真我','产品背景图','宠物','音乐','电商','图像去水印和文字','简易图像编辑'];

  // Create a map to hold the order index
  const orderMap = {};
  order.forEach((item, index) => {
    orderMap[item] = index;
  });

  // Sort the data based on the predefined order, with items not in the order array going last
  data.sort((a, b) => {
    const orderA = orderMap.hasOwnProperty(a.name) ? orderMap[a.name] : Number.MAX_SAFE_INTEGER;
    const orderB = orderMap.hasOwnProperty(b.name) ? orderMap[b.name] : Number.MAX_SAFE_INTEGER;
    return orderA - orderB;
  });
  return data
}

export function ImageModal({ imageUrl, closeModal }) {
  const containerRef = useRef(null);
  const imageRef = useRef(null);
  const [isDragging, setIsDragging] = useState(false);
  const [startX, setStartX] = useState(0);
  const [startY, setStartY] = useState(0);
  const [startScrollX, setStartScrollX] = useState(0);
  const [startScrollY, setStartScrollY] = useState(0);
  const [scale, setScale] = useState(1); // State to keep track of image scale

  useEffect(() => {
    const handleKeyDown = (event) => {
      if (event.key === 'Escape') {
        closeModal();
      }
    };

    document.addEventListener('keydown', handleKeyDown);
    return () => {
      document.removeEventListener('keydown', handleKeyDown);
    };
  }, [closeModal]);

  const onMouseDown = useCallback((event) => {
    event.preventDefault();
    setIsDragging(true);
    setStartX(event.pageX);
    setStartY(event.pageY);
    setStartScrollX(containerRef.current.scrollLeft);
    setStartScrollY(containerRef.current.scrollTop);
    containerRef.current.style.cursor = 'grabbing';
  }, []);

  const onMouseMove = useCallback((event) => {
    if (!isDragging) return;
    event.preventDefault();
    const x = event.pageX;
    const y = event.pageY;
    const walkX = (x - startX) * 3;
    const walkY = (y - startY) * 3;
    containerRef.current.scrollLeft = startScrollX - walkX;
    containerRef.current.scrollTop = startScrollY - walkY;
  }, [isDragging, startX, startY, startScrollX, startScrollY]);

  const onMouseUp = useCallback(() => {
    setIsDragging(false);
    containerRef.current.style.cursor = 'grab';
  }, []);

  const onMouseLeave = useCallback(() => {
    if (isDragging) {
      setIsDragging(false);
      containerRef.current.style.cursor = 'grab';
    }
  }, [isDragging]);

  const onDragStart = useCallback((event) => {
    event.preventDefault();
  }, []);

  const onWheel = useCallback((event) => {
    const newScale = scale + event.deltaY * -0.01;
    // Limit the scale between 0.5 and 3 for usability
    setScale(Math.min(Math.max(0.5, newScale), 3)); 
  }, [scale]);

  // Adjust the image dimensions based on the scale
  useEffect(() => {
    if (imageRef.current) {
      const naturalWidth = imageRef.current.naturalWidth;
      const naturalHeight = imageRef.current.naturalHeight;
      imageRef.current.style.width = `${naturalWidth * scale}px`;
      imageRef.current.style.height = `${naturalHeight * scale}px`;
    }
  }, [scale]);
  // Adjust the image dimensions based on the scale
  
  useEffect(() => {
    toast.success("滚轮放大缩小，拖动上下观察，ESC健或者右上角X关闭", { autoClose: 1800 });
  }, []);

  

  return (

    <div className="modal_max_image" style={{ overflow: 'auto', position: 'fixed', top: 0, left: 0,}} 
      onWheel={onWheel}
      >
        <button onClick={closeModal} style={{
            position: 'absolute',
            top: '10px',
            right: '10px',
            background: 'red',
            border: 'none',
            color: 'white',
            fontSize: '30px',
            cursor: 'pointer',
            outline: 'none',
            zIndex: '2000'
          }}
        >
          &#x2715;
        </button>
        <div className="modal-content_max_image"
            ref={containerRef}
            onMouseDown={onMouseDown}
            onMouseMove={onMouseMove}
            onMouseUp={onMouseUp}
            onMouseLeave={onMouseLeave}
            //onWheel={onWheel}
            style={{ maxWidth: '100%', overflow: 'auto', paddingTop: '40px' }}>
          <img src={imageUrl} ref={imageRef} alt="Original Picture"
              style={{ display: 'block', height: 'auto', width: 'auto' }}
              onDragStart={onDragStart} />
        </div>
      </div>


  );
}



