export function handleImageScale(img) {
  const maxDimension = 2048;  // Maximum size of the longest side

  // Get the original dimensions of the image
  const originalWidth = img.width;
  const originalHeight = img.height;

  // Determine the scaling factor
  const aspectRatio = originalWidth / originalHeight;
  let newWidth, newHeight;

  if (originalWidth > originalHeight) { // Landscape or square orientation
      newWidth = Math.min(originalWidth, maxDimension);
      newHeight = newWidth / aspectRatio;
  } else { // Portrait orientation
      newHeight = Math.min(originalHeight, maxDimension);
      newWidth = newHeight * aspectRatio;
  }

  // Calculate the scaling factor applied
  const scale = newWidth / originalWidth;

  // Return new dimensions and the scaling factor
  return {
      width: Math.round(newWidth),
      height: Math.round(newHeight),
      samScale: scale  // This is how much the image was scaled down
  };
}


export function loadImage(src) {
    return new Promise((resolve, reject) => {
        const img = new Image();
        img.onload = () => resolve(img);
        img.onerror = reject;
        img.src = src;
    });
}

export function resizeImage(img, width, height, file) {
  const fileData = {
    file: file,
    name: file.name,
    size: file.size,
    type: file.type,
  };
    return new Promise((resolve, reject) => {
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d');
        canvas.width = width;
        canvas.height = height;
        ctx.drawImage(img, 0, 0, width, height);

        canvas.toBlob(blob => {
            const resizedFile = new File([blob], fileData.name, {
                type: fileData.type,
                lastModified: Date.now()
            });

            // Replace original file with resized file in fileData
            const updatedFileData = {
                ...fileData,
                file: resizedFile,
                size: resizedFile.size
            };
            
            resolve(updatedFileData);
        }, fileData.type);
    });
}


export function generateUniqueFilename(path) {
  // Get the current datetime and format it as a string '20240503_123456'
  const now = new Date();
  const dateStr = now.getFullYear() +
                  ('0' + (now.getMonth() + 1)).slice(-2) +
                  ('0' + now.getDate()).slice(-2) + '_' +
                  ('0' + now.getHours()).slice(-2) +
                  ('0' + now.getMinutes()).slice(-2) +
                  ('0' + now.getSeconds()).slice(-2);

  // Generate a random UUID and convert it to a string
  const randomUuid = uuidv4().replace(/-/g, ''); // Removes dashes to match Python's uuid.hex
  
  // Create the unique prefix combining the date and UUID
  const uniquePrefix = `${dateStr}_${randomUuid}_`;

  // Extract the base name of the file from the path
  const baseName = path.split('/').pop(); // Assumes a '/' path delimiter

  // Prepend the unique prefix to the base name
  const newFilename = `${uniquePrefix}${baseName}`;

  return newFilename;
}

