export const fileDisplayAble = async (file) => {
  if (
    file.type.startsWith('image/') ||
    file.type.startsWith('video/') ||
    file.type.startsWith('audio/') ||
    (file.type === 'application/misc' && ['mp3', 'wav'].includes(getFileExtension(file.name))) ||
    (file.type === 'application/octet-stream' && ['png', 'jpg','jpeg','webp'].includes(getFileExtension(file.name))) ||
    (file.type === 'application/misc' && file.name.toLowerCase().endsWith('.mp4'))
  ) {
    return true
  }else{
    return false
  }
}



export const transformNodesFromFile = async (file) =>{
  return new Promise((resolve, reject) => {
    let reader = new FileReader();
    
    reader.onload = function(event) {
      try {
        let fileContent = JSON.parse(event.target.result);
        let transformedNodes = [];
        let annotator = 0
        let websocket = 0
        for (let key in fileContent) {
          if (fileContent.hasOwnProperty(key) && fileContent[key].class_type=='ComfyUIAppstoreParam') {
            let oldNode = fileContent[key];
            //let partentNode = oldNode.标注节点接入[0];
            //let oldNode = fileContent[partentNode];
            const matches = [0,oldNode.inputs && oldNode.inputs.序号,
              oldNode.inputs && oldNode.inputs.参数名称,
              oldNode.inputs && oldNode.inputs.类型,
              oldNode.inputs && oldNode.inputs.数值约束或依赖项,
              oldNode.inputs && oldNode.inputs.替换项]

            if (matches) {
              let newNode = tranformNodes(matches, oldNode.inputs && oldNode.inputs.标注节点接入[0])
              transformedNodes.push(newNode);
              annotator +=1
            }            
          }else if (fileContent.hasOwnProperty(key) && fileContent[key].class_type=='SaveImageWebsocket'){
            websocket +=1
          }
        }

        if (annotator < 1) {
          reject(new Error('文件不符合要求，至少包括一个ComfyUIAppstoreParam节点'));
        } else {
          transformedNodes.sort((a, b) => a.index - b.index);
          resolve(transformedNodes);
        }
      } catch (error) {
        reject(error);
      }
    };
    
    reader.onerror = function(error) {
      reject(error);
    };

    reader.readAsText(file);
  });
}


export const transformNodesFromFile_fromTitle = async (file) =>{
  return new Promise((resolve, reject) => {
    let reader = new FileReader();
    
    reader.onload = function(event) {
      try {
        let fileContent = JSON.parse(event.target.result);
        let transformedNodes = [];

        for (let key in fileContent) {
          if (fileContent.hasOwnProperty(key)) {
            let oldNode = fileContent[key];
            let title = oldNode._meta && oldNode._meta.title;

            if (title && title.startsWith("taoai_input")) {
              let regex = /taoai_input_{(.*?)}_{(.*?)}_{(.*?)}_{(.*?)}_{(.*?)}/;
              let matches = title.match(regex);

              if (matches) {
                let newNode = tranformNodes(matches, title)
                transformedNodes.push(newNode);
              }
            }
          }
        }
        transformedNodes.sort((a, b) => a.index - b.index);
        resolve(transformedNodes);
      } catch (error) {
        reject(error);
      }
    };
    
    reader.onerror = function(error) {
      reject(error);
    };

    reader.readAsText(file);
  });
}


