import React, { useEffect, useState } from 'react';
import styles from "../../Content/Agents/Agents.module.css";


const ImageList = ({ optionsObject, param, handleImageSelect, paramIndex }) => {
  const [displayedImages, setDisplayedImages] = useState(20); // Assuming 5 images per row, start with 2 rows (10 images)
  const imagesPerRow = 10; // Define how many images you want per row
  const rowMultiplier = 2; // Each click increases the number of displayed images by 2 rows

  const handleMoreClick = () => {
    setDisplayedImages(prev => prev + imagesPerRow * rowMultiplier); // Increase by two rows of images
  };

  const handleLessClick = () => {
    setDisplayedImages(20); // Reset to initial two rows (10 images)
  };
  const shouldShowMoreButton = optionsObject.length > displayedImages;
  const shouldShowLessButton = displayedImages > imagesPerRow * rowMultiplier;

  return (
    <div className={styles.image_list_container}>
      <div className={styles.image_selection_container}>
        {optionsObject.slice(0, displayedImages).map((option, index) => (
          <div key={index} className={styles.image_option}>
            <img
              src={option.option}
              alt={option.value}
              onClick={() => handleImageSelect(paramIndex, option.value)}
              className={param.value === option.value ? styles.image_selected : ''}
            />
          </div>
        ))}
      </div>
      <div className={styles.image_selection_container}>
        <br></br>
      </div>
      <div className={styles.button_container}>
        {shouldShowMoreButton && (
          <div className={styles.more_button} onClick={handleMoreClick}>
            更多
          </div>
        )}
        {shouldShowLessButton && (
          <div  className={styles.more_button} onClick={handleLessClick}>
            收回
          </div>
        )}
      </div>
    </div>
  );
};

export default ImageList;
