  const downloadapi = () => {
    console.log("MoneySetting initialized with response:", file);
    if (!file) return;
    const a = document.createElement("a");
    a.href = URL.createObjectURL(blob);
    a.download = file;
    a.click();
    URL.revokeObjectURL(a.href);
  };

