import React, { useEffect, useState, useRef } from 'react';
import styles from "../../Content/Agents/Agents.module.css";
import FileUpload from './fileuploader';
import { getLocalResourceFromURL } from "../../../utils/utils.jsx";

const ComboImageList = ({ optionsObject, param, paramIndex, handleImageSelect, handleInputChange, uploadPreviewUrl}) => {
  
  const imagesPerRow = useRef(0); // Calculate images per row based on parent width
  const rowMultiplier = 4; // Each click increases the number of displayed images by 2 rows
  const [displayedImages, setDisplayedImages] = useState(imagesPerRow.current*rowMultiplier); // Assuming 5 images per row, start with 2 rows (10 images)

  const [fileSelected, setFileSelected] = useState(param.type=='file')
  const firstOptionRef = useRef(null);
  const isMounted = useRef(true); // 用于跟踪组件的挂载状态

  const [localOptionsObject, setLocalOptionsObject] = useState(JSON.parse(optionsObject))
  const divRef = useRef(null)

  useEffect(() => {
    if (divRef && divRef.current) {
      imagesPerRow.current = Math.floor(window.innerWidth > 1000 ? (divRef.current.clientWidth-5) / (80+5) : (divRef.current.clientWidth-5) / (50+5))
      setDisplayedImages(imagesPerRow.current*rowMultiplier)
      // You can now use divWidth for any purpose within this component
    }
  }, [divRef.current]);

  const handleMoreClick = () => {
    setDisplayedImages(prev => prev + imagesPerRow.current * rowMultiplier); // Increase by two rows of images
  };

  const handleLessClick = () => {
    setDisplayedImages(imagesPerRow.current*rowMultiplier); // Reset to initial two rows (10 images)
  };
  
  const shouldShowMoreButton = optionsObject.length > displayedImages;
  const shouldShowLessButton = displayedImages > imagesPerRow.current * rowMultiplier;

  const handleFileSelected = (index, newValue) => {
    if (newValue) {
      setFileSelected(true)
    } else {
      setFileSelected(false)
      if (firstOptionRef.current) {
        firstOptionRef.current.click();
      }
    }
    handleInputChange(index, newValue)
  }


  // 处理 optionsObject 函数
  const transformOptionsObject = async (options) => {
    let newOptionsObject = [];

    for (const item of options) {
      if (!isMounted.current) break; // 如果组件已卸载，则终止循环

      const localresource = await getLocalResourceFromURL(item.value, item.option);
      if (localresource.url) {
        newOptionsObject.push({
          value: item.value,
          option: localresource.url,
        });
      } else {
        newOptionsObject.push(item); // 如果没有找到资源，添加 原来的。
      }
      console.log(item)
    }

    if (!isMounted.current) return null; // 循环结束后再次检查组件挂载状态
    return newOptionsObject.filter(option => option !== null); // 过滤掉所有 null 值
  };




  useEffect(()=>{
    if (param){
      if (param.type == 'file'){
        setfilevalue(param.value)
      }
    }
  }, [param])


  return (
    <div className={styles.image_list_container} ref={divRef}>
      <div className={styles.image_selection_container} >
        <div key={-1} className={styles.combo_image_option} 
          style={{ borderColor: fileSelected ? '#4CAF50' : '', border: fileSelected ? '6px solid #bf0f78' : '' }}>
          <FileUpload index={paramIndex} param={param} handleInputChange={handleFileSelected}
            previewUrl={uploadPreviewUrl} verbose={false} /> 
        </div>
        {localOptionsObject.slice(0, displayedImages - 1).map((option, index) => (
           <div key={index} className={styles.combo_image_option}>
           <div className={styles.imageContainer} 
                onClick={() => {
                 setFileSelected(false);
                 handleImageSelect(paramIndex, option.value);
               }}>
             <img
               src={option.option+'?x-oss-process=image/resize,w_100/quality,q_60'}
               alt={option.value}
               ref={index === 0 ? firstOptionRef : null}
               className={param.value === option.value ? styles.image_selected : ''}
             />
             <div className={styles.overlay} style={{overflow: 'hidden'}}>
               <p><span style={{fontSize:"8px", opacity:"100%",color:'#ffffff'}}>{option.value.split('/').pop().split('.')[0]}</span>
               </p>
             </div>
           </div>
         </div>
        ))}
      </div>
      <div className={styles.image_selection_container}>
        <br></br>
      </div>
      <div className={styles.button_container}>
        {shouldShowMoreButton && (
          <div className={styles.more_button} onClick={handleMoreClick}>
            更多
          </div>
        )}
        {shouldShowLessButton && (
          <div className={styles.more_button} onClick={handleLessClick}>
            收回
          </div>
        )}
      </div>
    </div>
  );
};

export default ComboImageList;
