import React, {useState, useMemo, useEffect, useCallback} from 'react';
import {downloadFile, downloadAllFiles, shareContent, returnResourceIcon} from "../../../utils/utils.jsx";
import Files from './FileList';
import { WEB_SITE } from '../../api/apiConfig.jsx';

export default function ResourceList({files, channel, runs, runid,selectedRunStatus}) {
  const [selectedRunId, setSelectedRunId] = useState(null);

  const filesByRun = useMemo(() => runs.map(run => {
    const relatedFiles = files.filter(file => file.agent_execution_id === run.id);
    return relatedFiles.length !== 0 && {"run": run, "files": relatedFiles};
  }).filter(Boolean), [files, runs]);

  const downloadRunFiles = (run_id, name) => {
    const runFiles = files.filter(file => file.agent_execution_id === run_id);
    runFiles.length !== 0 && downloadAllFiles(runFiles, name);
  }

  const isAnyFileWithAgentId = files.some(file => file.agent_execution_id !== null);
 
  return (
    <div id="resource-list">
      {channel === 'output' && (!isAnyFileWithAgentId || files.length <= 0 ?
          <div className="horizontal_space_between flex_wrap">

            {(selectedRunStatus !== "ERROR_PAUSED" && selectedRunStatus !== "COMPLETED" && selectedRunStatus !== "TERMINATED" && selectedRunStatus !== "ITERATION_LIMIT_EXCEEDED") ?
              <>
                <div><span className="feed_title mt_8">尚未执行完毕.或者没有输出文件.</span></div>
                <div><img style={{width:'34px', height:'34px'}} loading='lazy' className="mix_blend_mode" src={`${WEB_SITE}/images/loading.gif`} alt="active-icon" tyle={{ float: "left" }} /></div>
              </>:
              <>
              <div><span className="feed_title mt_8">已经结束！</span></div>
            </>
            }
        </div>
          :
          <div id="division_by_runs" className="horizontal_space_between flex_wrap">
            {filesByRun.map((filesRun, index) => (
              <div key={filesByRun.length - index - 1} style={{width:"100%"}}>
                {filesRun.run.id === runid && 
                <>
                  <div className="horizontal_container justify_space_between cursor_pointer padding_8_6"
                      onClick={() => setSelectedRunId(filesRun.run.id === runid ? null : filesRun.run.id)}>
                  
                    <div className="horizontal_container">
                      <img src={runid === filesRun.run.id ? `${WEB_SITE}/images/arrow_downward_dropdown.svg` : `${WEB_SITE}/images/arrow_forward.svg`}
                        alt="arrow" style={{width:'14px', height:'14px'}}/>
                    </div>
                    <img src={`${WEB_SITE}/images/download.svg`} alt="download_icon" style={{width:'20px', height:'21px'}} onClick={() => downloadRunFiles(filesRun.run.id, filesRun.run.name)}/>
                  </div>
                  
                  <Files files={filesRun.files} downloadFile ={downloadFile} returnResourceIcon= {returnResourceIcon} shareContent={shareContent}/>
                  
                </>
                }
              </div>
            ))}
          </div>
      )}

      {channel === 'input' && files &&
          <Files files={files} downloadFile ={downloadFile} returnResourceIcon= {returnResourceIcon}/>
        }
    </div>
  )
}