import styles from "../../Content/Agents/Agents.module.css";
import React, { useEffect, useState, useRef } from 'react';
import { getFileObject, saveFile,getLocalResourceURL, handleImageScale, loadImage, resizeImage,transformNodesFromFile } from "../../../utils/utils.jsx";
import FileUpload from "./fileuploader.jsx";
import ImageList from "./imagelist.jsx";
import ComboImageList from "./comboimagelist.jsx";
import { getTemplateResource } from "../../api/DashboardService.jsx";
import Cookies from "js-cookie";
import Segment from "../../Content/Segment/Segment.jsx"
import EditableImage from "./EditableImage.jsx";

const DynamicParameterRender = ({ params, setParamFunc, template_id = null, is_author = false}) => {
  // when all the resources are saved on the server, set the template_id. 
  // when all the resources are not on the server, set the template_id as null.
  const userType = Cookies.get('userType')
  const [previewUrls, setPreviewUrls] = useState([]);
  const [selectedImages, setSelectedImages] = useState({});
  const cancelInputChangeTokenRef = useRef(null); // 使用useRef来存储取消令牌
  const [isFlex,setIsFlex] = useState(true);
  // 在useEffect外部声明AbortController
  let controller = new AbortController();

  useEffect(() => {

    // 在每次useEffect运行时创建新的AbortController实例
    controller = new AbortController();
    const signal = controller.signal;

    fetchUpdatedParams(params, signal).then(updatedParams => {
      if (signal.aborted) {
        throw new Error('Fetch aborted');
      }
      // Update only if necessary to avoid infinite loops
      if (JSON.stringify(params) !== JSON.stringify(updatedParams)) {
        setParamFunc && setParamFunc(updatedParams);
      }
      //console.log(updatedParams);  // Use the updated parameters here
    }).catch(error => {
      console.error("Failed to update parameters:", error);
    });
    
    // 在下一次useEffect运行时中止前一个fetchUpdatedParams的执行
    return () => {
      controller.abort();
    };
  }, [params]);

  // save the parameters. 
  const handleInputChange = async (index, newValue) => {
    //首先先清理一下依赖项。
    // 这里有个bug， 就是会有多个dependent， 我们先不处理
    const newToken = {}; // 创建一个新的取消令牌
    cancelInputChangeTokenRef.current = newToken; // 直接在引用中设置新的取消令牌
    
    const dependentIndex = params.findIndex(p => p.maskSourceUrl === params[index].name);
    let newDependentValue = null
    if (dependentIndex >=0){
      //newDependentValue = {target: {name:null, files:[null, null]}}
      newDependentValue = null
    }
    const updatedParams = await Promise.all(params.map(async (param, paramIndex) => {
      try{
        if (index === paramIndex || paramIndex==dependentIndex) {
          if (param.type === 'file' || param.type === 'combo_image_selection'|| param.type === 'image_editer') {
  
            if (newValue===undefined || newValue == null) {
              // 当没有数值的时候我们就删除掉之前的内容。 // todo, 我们这样可能要删掉从网站上下载下来的内容。
              // await getFileObject(`${param.value.name}_${param.value.type}_${param.value.size}`, true);
              const { value, ...newParam } = param;
              param = newParam
            } else if (newValue.target.files[0]) {
              try {
                let file = newValue.target.files[0];
                // Create an object URL for the image file
                const objectURL = URL.createObjectURL(file);

                if (file.type.startsWith('image/')) {
                  const img = await loadImage(objectURL);
                  const { height, width, samScale } = handleImageScale(img);
                  file = await resizeImage(img, width, height, file);
                  saveFile(file.file, `${file.name}_${file.type}_${file.size}`);
                }else{
                  file = {
                    file: file,
                    name: file.name,
                    size: file.size,
                    type: file.type,
                  };
                  saveFile(file.file, `${file.name}_${file.type}_${file.size}`);
                }
                // Cleanup object URL
                URL.revokeObjectURL(objectURL);

                param  = { ...param, value: file };
              } catch (error) {
                  console.error('Error processing image:', error);
                  URL.revokeObjectURL(objectURL);
                  throw error;  // Re-throw to handle it in higher level catch or error handling logic
              }
            }
            
            return param

          } else if (param.type === 'mask') {
            if (paramIndex==dependentIndex) newValue = newDependentValue
            if (!newValue) {
              // 当没有数值的时候我们就删除掉之前的内容。   暂时不删除 。 保留下。
              /*
              if (Array.isArray(param.value) && param.value.length >=1){
                for (let i = 0; i < param.value.length; i++) {
                  if (param.value[i]){
                    const file = param.value[i];
                    await getFileObject(`${file.name}_${file.type}_${file.size}`, true);
                  }
                }
              }*/
              param =  { ...param, value: [null,null] };              
              return param
            } else if (newValue.target.files.length > 0) {
              let data = []
              for (let i = 0; i < newValue.target.files.length; i++) {
                const file = newValue.target.files[i];
                if (file){  // sometimes we dont have the mask but we have the embeddings. 
                  const fileData = {
                      file: file,
                      name: file.name,
                      size: file.size,
                      type: file.type,
                  };
                  // Concatenate file name, type, and size to create a unique identifier
                  const identifier = `${fileData.name}_${fileData.type}_${fileData.size}`;
                  // Save each file with the constructed identifier
                  await saveFile(fileData.file, identifier); 
                  data.push(fileData)
                  // Optionally update param's value with the file data or other relevant data
                  // This depends on how you want to use the updated param value in your application
                }else{
                  if (file==""){
                    data.push(param.value?.[i])  // we should not set this to null. we should keep the old value. 
                  }else{
                    data.push(null)  // we should not set this to null. we should keep the old value. 
                  }

                }
              }
              param ={ ...param, value: data }; 
              return param
            } else {
              return param;
            }
          } else if (param.type === 'workflow') {
            if (paramIndex==dependentIndex) {
              // 这里我们要把
              if (newValue){ // 父节点发生了变化
                try{
                  newValue = await changeWorkflowToParamers(newValue.target.files[0])
                } catch(error){
                  console.error('workflow is error:', error);
                  newValue = []
                }
              }else{
                newValue = []
              }
            }
            param ={ ...param, value: newValue }; 
            return param
          } else {
            param ={ ...param, value: newValue }; 
            return param
          }
        }
        return param;
      } catch (error) {
        console.error('Error handling input change:', error);
      }
    }));

    if (cancelInputChangeTokenRef.current!= newToken){
      console.log('canceld input change: params are ', updatedParams)
    }
     
    setParamFunc && setParamFunc(updatedParams);
    
  };
  
  const changeWorkflowToParamers = async (file) => {
    try {
      if (!file) {
        return []
        
      }
  
      let transformedNodes = await transformNodesFromFile(file);
      console.log('Returned transformed nodes:', transformedNodes);
      return transformedNodes;
    } catch (error) {
      console.error("Error:", error);
      alert(error)
      throw error; // Re-throw the error to propagate it
    }
  };

  const fetchFileUrl = async (param, index, signal) => {
    // when we have chosen a file. 
    // Check if the fetch operation should be aborted
    if (signal.aborted) {
      throw new Error('Fetch aborted');
    }
    if ((param.type === 'file' || param.type === 'combo_image_selection' || param.type ==='image_editer') && param.value ) {
      let key = `${param.value.name}_${param.value.type}_${param.value.size}`
      let url = param.value.name ?(await getLocalResourceURL(`${param.value.name}_${param.value.type}_${param.value.size}`, template_id)).url:null;
      if (key != `${param.value.name}_${param.value.type}_${param.value.size}`){
        console.error('changed '+ key)
        throw new Error('Fetch aborted already changed. ');
      }
      // Check if the fetch operation should be aborted
      if (signal.aborted) {
        throw new Error('Fetch aborted');
      }
      // Update previewUrls with the retrieved URL
      if (url) {
        // set all the related images . 
        const fileParamIndex = params.findIndex(p => p.maskSourceUrl === param.name);
        
        setPreviewUrls(prevUrls => {
          const newPreviewUrls = [...prevUrls];
          newPreviewUrls[index] = url;

          if (fileParamIndex>=0){
            newPreviewUrls[fileParamIndex] = `${param.value.name}_${param.value.type}_${param.value.size}` //这里当file变化时， 我们把对应的mask的url都设置成一个。
          }
          return newPreviewUrls;
        });
      }
    } else if ((param.type === 'file' || param.type === 'combo_image_selection' || param.type === 'mask' || param.type === 'image_editer') && !param.value) {
      setPreviewUrls(prevUrls => {
        const newPreviewUrls = [...prevUrls];
        newPreviewUrls[index] = null;
         // set all the related images . 
         const fileParamIndex = params.findIndex(p => p.maskSourceUrl === param.name);
         if (fileParamIndex>=0){
          newPreviewUrls[fileParamIndex] = null;
         }
        return newPreviewUrls;
      });
    }
    return param  // we have change the file the first time we load the template from the history. 
  };

  const fetchUpdatedParams = async (params, signal) => {
    const updates = params.map(async (param, index) => {  // Use async inside map
      // Check if the fetch operation should be aborted
      if (signal.aborted) {
        throw new Error('Fetch aborted');
      }
      if ((param.type === 'selection' || param.type === 'image_selection') && !param.value) {
        try {
          const optionsObject = JSON.parse(param.options || param.default);
          if (Array.isArray(optionsObject)) {
            return { ...param, value: optionsObject[0].value };
          }
        } catch (e) {
          const defaultValue = (param.options || param.default || '').split(',')[0].trim();
          return { ...param, value: defaultValue };
        }
      } else if (param.type === 'text' && (param.value === undefined || param.value == null)) {
        return { ...param, value: param.default };
      } else if (param.type === 'file' || param.type === 'combo_image_selection' || param.type === 'mask'|| param.type === 'image_editer') {
        return await fetchFileUrl(param, index, signal)
      }
      return param;
    });

    if (signal.aborted) {
      throw new Error('Fetch aborted');
    }
    return Promise.all(updates);  // Wait for all promises to resolve
  };

  const handleInputChangeWithIndex = (index) => async (newValue) => {
    await handleInputChange(index, newValue);
  };
  

  // Function to handle image selection
  const handleImageSelect = (index, imageUrl) => {
    const newSelectedImages = { ...selectedImages, [index]: imageUrl };
    setSelectedImages(newSelectedImages);
    // Update the params with the selected image
    const updatedParams = params.map((param, paramIndex) => {
      if (index === paramIndex && (param.type === 'image_selection' || param.type === 'combo_image_selection')) {
        return { ...param, value: imageUrl };
      }
      return param;
    });

    setParamFunc && setParamFunc(updatedParams);
  };

  // Function to generate form inputs based on params
  const renderFormInputs = () => {
    const renderInputs = params.map((param, index) => {
      switch (param.type) {
        case 'text':
          return (
              <div key={index + JSON.stringify(param)}
                   style={{height: ((userType == 'user' || userType == 'guest') && param.visibleToUser == 0) ? '0px' : 'fit-content'}}
              >
                <div  className={styles.form_label_con}>
                  <label className={styles.form_label_text} htmlFor={param.name} style={{fontSize:'12px'}}>{param.name}: </label>
                </div>
                {/*<div className={styles.item_title_area} style={{padding: '10px 10px 0 10px'}}>*/}
                {/*  <label className={styles.form_label_text} htmlFor={param.name}>{param.name}3333: </label>*/}
                {/*</div>*/}
                <textarea rows={1}
                          className="parameter_input_medium"
                          style={{display:'block',marginLeft:'20px'}}
                          type="text"
                          name={param.name}
                          placeholder="请输入文字，地方不够，请拖动右下角"
                          defaultValue={param.value || param.default ||param.accept || ''} // 在
                          onBlur={(e) => handleInputChange(index, e.target.value)} // Using onBlur to update on input lose focus
                          onInput={(e) => {
                            e.target.style.height = 'auto'; // Reset height to auto to measure the content height accurately
                            e.target.style.height = e.target.scrollHeight + 'px'; // Set height to match the content height
                          }}
                          onFocus={(e) => {
                            e.target.style.height = 'auto'; // Set height to auto when focused to allow for dynamic height adjustment
                            e.target.style.height = e.target.scrollHeight + 'px'; // Set height to match the content height
                          }}
                />
              </div>
          );
        case 'selection':
          return (
              <div key={index + JSON.stringify(param)}>
                <div className={styles.form_label_con} style={{marginTop: '20px'}}>
                  <i></i>
                  <label className={styles.form_label_text} htmlFor={param.name}>{param.name}: </label>
                </div>
                {/*<div className={styles.item_title_area} style={{padding: '5px 10px'}}>*/}
                {/*  <label className={styles.form_label_text} htmlFor={param.name}>{param.name}: </label>*/}
                {/*</div>*/}

                <select
                    name={param.name}
                    className="dropdown_container_search"
                    value={param.value} // Setting value to ensure it's controlled
                    onChange={(e) => handleInputChange(index, e.target.value)}
                >
                  {/**param.options can be two kind of string, first is a comma seperated string, second can be a json string like : {value:{value}, "option":"optionurl"}.  in the first situation, we  jst make the value of the option simply the splitted string, if it's the second situation, put the value as the value and the option as the option string. */}
                  {
                    (() => {
                      // Attempt to parse param.options as JSON
                      try {
                        const optionsObject = JSON.parse(param.options || param.default);
                        console.log(1)
                        if (Array.isArray(optionsObject)) {
                          // If the parsed JSON is an array, render each image URL in the array
                          return optionsObject.map((option, optionindex) => (
                                  <option key={optionindex} value={option.value}>{option.option}</option>
                              )
                          );
                        }
                      } catch (e) {
                        // If JSON parsing fails, treat it as a comma-separated string
                        return (param.options || param.default || '').split(',').map((option, optionIndex) => (
                            <option key={optionIndex} value={option.trim()}>
                              {option.trim()}
                            </option>
                        ));
                      }
                    })()
                  }

                </select>
              </div>
          );
        case 'image_selection':
          return (
              <div key={index + JSON.stringify(param)}>
                <div className={styles.item_title_area} style={{padding: '5px 10px'}}>
                  <label className={styles.form_label_text} htmlFor={param.name}>{param.name}: </label>
              </div>
              <>
                { /**
                  To modify the given code snippet to handle both situations where param.options can either be a comma-separated string of image URLs or a JSON string representing an object with value and option keys, you can follow a similar approach to the previous solution. Here, you'll parse the param.options as JSON if possible, and based on the content, render the images or a single image accordingly. */
                  (() => {
                    try {
                      const optionsObject = JSON.parse(param.options || param.default || '{}');
                      if (Array.isArray(optionsObject)) {
                        // If the parsed JSON is an array, render each image URL in the array

                        return <ImageList optionsObject={optionsObject} param={param}
                          handleImageSelect={handleImageSelect} paramIndex={index}>

                        </ImageList>
                        return optionsObject.map((option, imageIndex) => (
                          <div key={imageIndex} className={styles.image_option}>
                            <img
                              src={option.option}
                              alt={option.value}
                              onClick={() => handleImageSelect(index, option.value)}
                              className={param.value === option.value ? styles.image_selected : ''}
                            />
                          </div>
                        )
                        );
                      }
                    } catch (e) {
                      // Treat as a comma-separated string
                      return (param.options || param.default || '').split(',').map((imageUrl, imageIndex) => (
                        <div key={imageIndex} className={styles.image_option}>
                          <img
                            key={imageIndex} 
                            src={imageUrl.trim()}
                            alt={`Option ${imageIndex + 1}`}
                            onClick={() => handleImageSelect(index, imageUrl.trim())}
                            className={param.value === imageUrl.trim() ? styles.image_selected : ''}
                          />
                        </div>
                      ));
                    }
                  })()
                }

              </>
            </div>
          );
        case 'combo_image_selection':
          return (
              <div key={index + JSON.stringify(param)}>
                <div className={styles.form_label_con} style={{marginTop:'20px'}}>
                  <i></i>
                  <label className={styles.form_label_text} htmlFor={param.name}>{param.name}: </label>
                </div>
                {/*<div className={styles.item_title_area}>*/}
                {/*  <label className={styles.form_label_text} htmlFor={param.name}>{param.name}222: </label>*/}
                {/*</div>*/}
                <>

                  <ComboImageList key={index} optionsObject={param.options || param.default || '[]'} param={param}
                                  paramIndex={index}
                                  handleImageSelect={handleImageSelect} handleInputChange={handleInputChange}
                                  uploadPreviewUrl={previewUrls[index]}>
                  </ComboImageList>

                </>
              </div>
          );
        case 'file':
          return (
              <div key={index + JSON.stringify(param)} style={{
                minHeight: window.innerWidth > 1000 ? ((param.name == '图标' || param.accept.includes('json')) ? '100px' : '550px') : ((param.name == '图标'|| (param.accept && param.accept.includes('json'))) ? '30px' : '350px'),
                height: (userType != 'admin' && param.visibleToUser == 0 && !is_author)?'0px':'fit-content'}}>
                {((userType == 'admin' || param.visibleToUser !== 0 || is_author) &&(param.name != '图标')) && (
                  <div className={styles.item_title_area} style={{padding:'20px'}}>
                  <label className={styles.form_label_text} htmlFor={param.name}>{param.name}: </label>
                  </div>
                )}
              <div style={{height: window.innerWidth>1000?(userType!='admin' && param.visibleToUser == 0 && !is_author)?'0px':((param.name == '图标'|| (param.accept && param.accept.includes('json')))?'60px':'500px'):(userType!='admin' && param.visibleToUser == 0 && !is_author)?'0px':((param.name == '图标'|| param.accept.includes('json'))?'60px':'300px') }}>
              {setParamFunc &&
                <FileUpload key={index}  index={index} param={param} handleInputChange={handleInputChange} previewUrl={previewUrls[index]} 
                visible={userType=='admin' || param.visibleToUser != 0 || is_author} verbose={true}></FileUpload>
              }
              </div>
            </div>
          );
        case 'image_editer':
          return (
            <div key={index + JSON.stringify(param)} style={{minHeight: window.innerWidth>1000?'550px':'350px', height: 'fit-content',width: '100%', minWidth: 'fit-content'}} id='dddd'>
              <div className={styles.item_title_area} style={{padding:'10px 10px 0 10px'}}>
              <label className={styles.form_label_text} htmlFor={param.name}>{param.name}: </label>
              </div>
              {setParamFunc &&
                <EditableImage  key={index} index={index} param={param} handleInputChange={handleInputChange} previewUrl={previewUrls[index]}></EditableImage>

              }
            </div>
          );
        case 'mask':
          return (
            <div key={index} style={{minHeight: window.innerWidth>1000?'550px':'350px', height: 'fit-content'}}>
               <div className={styles.item_title_area} style={{padding:'10px 10px 0 10px'}}>
              <label className={styles.form_label_text} htmlFor={param.name}>{param.name}: </label>
              </div>
              <div style={{height: window.innerWidth>1000?'500px':'300px' }}>
              {/* Pass the previewUrl as the sourceurl prop to the Segment component */}
              {setParamFunc &&
                <Segment key={index}  param={param} index={index} handleInputChange={handleInputChange} sourceImageID={previewUrls[index]} template_id={template_id}/>
              }
              </div>
            </div>
          );
        case 'workflow':
          return (
              <div key={index + JSON.stringify(param)} style={{height: 'fit-content'}}>
                <div className={styles.form_label_con} style={{}}>
                  <i></i>
                  <label className={styles.form_label_text} htmlFor={param.name}>{param.name}
                    : </label>
                </div>
                {/*<div className={styles.item_title_area}>*/}
                {/*  <label className={styles.form_label_text} htmlFor={param.name}>{param.name}1112*/}
                {/*    : </label>*/}
                {/*</div>*/}
                <div style={{height: 'fit-content'}}>
                  {setParamFunc &&
                      <DynamicParameterRender key={index + JSON.stringify(param) +'workflow'} params={param.value} index={index}
                                              setParamFunc={handleInputChangeWithIndex(index)}
                                              template_id={template_id}/>
                  }
                </div>
              </div>
          );

        default:
          return null;
      }
    });
    const renderGroups = [];
    let i = 0;
    let singleLineComponents = ['image_selection', 'combo_image_selection', 'workflow'];

    while (i < renderInputs.length) {
      // Check if the current fragment is an "image_selection" fragment

      if ((singleLineComponents.includes(params[i].type))) {

        // Render the "image_selection" fragment in a single line

          renderGroups.push(
              <div key={i} style={{marginBottom: '25px', height: 'fit-content'}} className='rightItenBottom'>
                {renderInputs[i]}
              </div>
          );

        i++;
      } else {
        // 当且仅当本行是file，而且下一行不是file的时候我们不并列，
        if (((userType == 'user' || userType == 'guest') && params[i].visibleToUser == 0) || (params[i].type === 'file' && (i + 1 < renderInputs.length && renderInputs[i + 1] && !['file','mask'].includes(params[i + 1].type)))) {
          renderGroups.push(
            <div className='leftItemTop'>
              <div className={styles.form_label_con}>
                <i></i>
                <label className={styles.form_label}>参数<span
                  style={{ fontSize: '9px' }}>&nbsp;(选填)</span></label>
              </div>
              <div  key={i} className={styles.compileCon} style={{ display: 'flex', marginBottom: '5px', height: (userType != 'admin' && params[i].visibleToUser == 0 && !is_author)?'0px':'fit-content'}}>
                <div style={{ width: '90%', height: (userType != 'admin' && params[i].visibleToUser == 0 && !is_author)?"0px":'fit-content' }}>{renderInputs[i]}</div>
              </div>
            </div>
          );
          i++;
        } else {
          // Render two fragments per line
          renderGroups.push(

            <div
                id='asasad'
                key={i}
                className={styles.compileCon}
                style={{
                  // display: 'flex',
                  width:'90%',
                  marginBottom: '5px',
                  height: 'fit-content',
                  marginTop:(params[i].name==='图标')?'30px':'0',
                  boxShadow:(params[i].name==='图标')?'none':'0px 2px 4px 0px rgba(0,0,0,0.16)',
                  border:(params[i].name==='图标')?'1px solid #afafbc':'none',
                  borderRadius:(params[i].name==='图标')?'10px':'0 0 10px 10px',
                  marginLeft:(params[i].name==='图标')?'10px':'0'
                }}
            >
              <div
                  style={{
                    // width: (i + 1 < renderInputs.length && renderInputs[i + 1]) ? '50%' : '100%' ,
                    height: 'fit-content'
              }}>{renderInputs[i]}</div>
              <div style={{ height: 'fit-content' }}>{i + 1 < renderInputs.length && renderInputs[i + 1]}</div>
            </div>
          );
          i += 2;
        }        
      }
    }


    return renderGroups;
  };

  return (
      //dynamicForm
    <form id='dynamicForm' style={{
      width: '100%',
      // display: 'flex',
      // flexWrap:'wrap',
      // justifyContent:'space-between'
    }}>
       {renderFormInputs()}
    </form>
  );
};

export default DynamicParameterRender;
