import React, { useEffect, useState, useRef } from 'react';
import WorkerAdmin from './WorkerAdmin.jsx';
import { WEB_SITE } from './pages/api/apiConfig.jsx';

const App = () => {
  const [menuPosition, setMenuPosition] = useState({ right: 10, top: 120 });
  const [showDrawer, setshowDrawer] = useState(false);
  const isDragging = useRef(false);
  const initialMousePos = useRef({ x: 0, y: 0 });

  // 手动实现节流函数
  const throttle = (func, delay) => {
    let lastCall = 0;
    return (...args) => {
      const now = new Date().getTime();
      if (now - lastCall < delay) return;
      lastCall = now;
      func(...args);
    };
  };

  const handleMouseDown = (e) => {
    if (e.button !== 0) return; // Only allow left-click
    e.stopPropagation();
    isDragging.current = true;
    initialMousePos.current = { x: e.clientX, y: e.clientY };
  };

  const handleMouseMove = throttle((e) => {
    if (!isDragging.current) return;

    setMenuPosition((prevPosition) => {
      const newRight = prevPosition.right - (e.clientX - initialMousePos.current.x);
      const newTop = prevPosition.top + (e.clientY - initialMousePos.current.y);
      initialMousePos.current = { x: e.clientX, y: e.clientY };
      return { right: newRight, top: newTop };
    });
  }, 30); // Adjust delay (in ms) as needed

  const handleMouseUp = () => {
    if (isDragging.current) {
      isDragging.current = false;
      console.log("Mouse Up: Stop Dragging");
    }
  };

  useEffect(() => {
    document.addEventListener('mousemove', handleMouseMove);
    document.addEventListener('mouseup', handleMouseUp);
    return () => {
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('mouseup', handleMouseUp);
    };
  }, []);

  return (
    <div>
      <WorkerAdmin key={showDrawer} isvisible={showDrawer} />

      {/* Draggable Right Menu */}
      <div
        style={{
          display: 'flex',
          flexDirection: 'column',
          position: 'fixed',
          zIndex: 1999999999999999,
          backgroundColor: 'transparent',
          right: `${menuPosition.right}px`,
          top: `${menuPosition.top}px`,
          height: '60px',
          cursor:'pointer'
        }}
        onMouseDown={handleMouseDown} // Attach to parent div
      >
        <div style={{ borderRadius: '10px', height: '30px' }}>
          <img
            style={{
              borderRadius: '10px',
              height: '100%',
              pointerEvents: 'none',
            }}
            src={`${WEB_SITE}/logo/newlogo1.png`}
          />
        </div>
        <div
          className="secondary_button"
          onClick={() => setshowDrawer(!showDrawer)}
        >
          {showDrawer?'收起':'变现'}
        </div>
      </div>
    </div>
  );
};

export default App;
