import React, { useEffect, useState } from 'react';
import { useRef } from 'react';
import Cookies from "js-cookie";
import { MediaDisplay } from '../../../utils/utils.jsx';

// Inside your functional component
function FileUpload({ index, param, handleInputChange, previewUrl,visible=true, verbose=true }) {
  const userType = Cookies.get('userType')
  const [isDragging, setIsDragging] = useState(false);
  const [disabled, setDisabled] = useState(true);

  const fileInputRef = useRef(null);
  const parentDiv = useRef(null);

  const handleDragEnter = (e) => {
    e.preventDefault();
    setIsDragging(true);
  };

  const handleDragLeave = (e) => {
    e.preventDefault();
    setIsDragging(false);
  };

  const handleDragOver = (e) => {
    e.preventDefault();
  };

  const handleDrop = (e) => {
    e.stopPropagation(); // 阻止事件冒泡
    if (disabled){
      alert("请稍等加载中")
      return 
    }

    e.preventDefault();
    setIsDragging(false);
    const file = e.dataTransfer.files[0];
    if (file) {
      /*
      if (file.name.length > 30) {
        alert('文件名称过长，请上传文件名不超过30个字符的文件。');
        return; // Stop further execution if the file name is too long
      }*/
      const validExtensions = param.accept.split(',');
      const fileExtension = file.name.split('.').pop().toLowerCase();
      if (validExtensions=="" || validExtensions.includes(`.${fileExtension}`)) {
          const syntheticEvent = {
              target: {
                  name: param.name,
                  files: [file],
              },
          };
          handleInputChange(index, syntheticEvent);
      } else {
          alert('文件类型暂不支持，请上传如下类型文件：'+param.accept);
      }
    }
  };

  const handleImageChange = (e) => {
    e.preventDefault();
    const file = e.target.files[0];
    if (file) {
      /*
      if (file.name.length > 30) {
        alert('文件名称过长，请上传文件名不超过30个字符的文件。');
        return; // Stop further execution if the file name is too long
      }*/
      handleInputChange(index, e); 
    }
  };

  const handleCancel = () => {
    handleInputChange(index, null); 
  };

  useEffect(() => {
    if (param.type == 'file' && param.value  && !previewUrl){
      setDisabled(true)
    }else{
      setDisabled(false)
    }
  }, [previewUrl]);

  return (
    <div className={`my-file-drop-area ${isDragging ? 'dragging' : ''}`} 
        onDragEnter={handleDragEnter}
        onDragLeave={handleDragLeave}
        onDragOver={handleDragOver}
        onDrop={handleDrop}
        onClick={() => {
          if (false){
            alert("请稍等加载中")
          }else{
            fileInputRef.current.click()
          }
        }} // Trigger file input click on area click
        style={{ position: 'relative', width: '100%', height:'100%', maxWidth: '100%', maxHeight: '100%' ,background:'none'}} // Ensure the container resizes with the image
    >
    <input
        type="file"
        ref={fileInputRef}
        style={{ display: 'none' }}
        name={param.name}
        accept={param.accept}
        onChange={(e) => {
          if (false){
            alert("请稍等加载中")
          }else{
            handleImageChange(e)
          }
        }}
    />
    {previewUrl ? (
        //<div className="preview" style={{ position: 'relative', maxWidth: '100%', maxHeight: '100%' }}>
        <>
        {(visible) &&
        <div style={{
          width: '80%',
          height: '100%',
          objectFit: 'contain',
          position: 'absolute',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
        }} ref={parentDiv}>
          <MediaDisplay
            url={previewUrl}
            content={param.value?.name?param.value.name:''}
            type={param.value?.type?param.value.type:'image'}
            alt="Preview"
          />
          <button
              type="button" // Specify button type as "button"
              className="cancel"
              onClick={(e) => {
                  e.stopPropagation(); // Prevent click event from bubbling up to the file drop area
                  fileInputRef.current.value = '';
                  handleInputChange(index, null); // Call the close handler
              }}
              style={{
                  position: 'absolute',
                  top: '0px',
                  right: '10px',
                  padding: '2px',
                  fontSize: '10px',
                  borderRadius: '5%',
                  border: 'none',
                  cursor: 'pointer',
              }}
          >
              X
          </button>
          </div>
          }
        </>
    ) : (
      <>
      {disabled ?(
        <p style={{ textAlign: 'center', margin: 0 , fontSize: '12px'}}>
          <img className="mix_blend_mode mr_7"  loading="lazy" width={14} height={14} src="/images/loading.gif" alt="loading-icon"/>加载中，稍等。或者点击上传新的图像</p>
      ):(
        <p style={{ textAlign: 'center', margin: 0 , fontSize: '12px'}}>{(window.innerWidth > 1000 ) ? '点击上传， 或者拖入文件：'+(param.accept?param.accept:'.json') : verbose?'点击上传， 或者拖入文件':'+'}</p>
      )}
      </>
    )}
    
    </div>


  );
}

export default FileUpload;
