export  const uploadFile = async(agentId, formData) => {
  let key1= Cookies.get('accessToken')
  let key= localStorage.getItem('accessTokenHuaxiaobao')
  try {
    const response = await axios.post(`${baseUrl()}resources/add/${agentId}`, formData, {
        headers: {
            Authorization: `Bearer ${key}`, // Replace with actual token variable
            // Do NOT set Content-Type explicitly here; let Axios handle it for FormData
        },
    });
    return response.data;
  } catch (error) {
      console.error("Upload failed:", error.response ? error.response.data : error.message);
  }

  //
  //return api.post(`/resources/add/${agentId}`, formData);
};

