  function applySobelFilter(data, width, height) {
    const sobelData = new Uint8ClampedArray(data.length);

    // Sobel X Filter
    const xKernel = [
        -1, 0, 1,
        -2, 0, 2,
        -1, 0, 1
    ];

    // Sobel Y Filter
    const yKernel = [
        -1, -2, -1,
         0,  0,  0,
         1,  2,  1
    ];

    // Apply Sobel filter
    for (let y = 1; y < height - 1; y++) {
        for (let x = 1; x < width - 1; x++) {
            const pixelIndex = (y * width + x) * 4;

            let gxRed = 0, gyRed = 0;
            let gxGreen = 0, gyGreen = 0;
            let gxBlue = 0, gyBlue = 0;

            for (let kernelY = -1; kernelY <= 1; kernelY++) {
                for (let kernelX = -1; kernelX <= 1; kernelX++) {
                    const weightX = xKernel[(kernelY + 1) * 3 + (kernelX + 1)];
                    const weightY = yKernel[(kernelY + 1) * 3 + (kernelX + 1)];
                    const neighborX = x + kernelX;
                    const neighborY = y + kernelY;
                    const neighborIndex = (neighborY * width + neighborX) * 4;

                    gxRed += data[neighborIndex] * weightX;
                    gyRed += data[neighborIndex] * weightY;
                    gxGreen += data[neighborIndex + 1] * weightX;
                    gyGreen += data[neighborIndex + 1] * weightY;
                    gxBlue += data[neighborIndex + 2] * weightX;
                    gyBlue += data[neighborIndex + 2] * weightY;
                }
            }

            // Calculate gradient magnitudes for each channel
            const magnitudeRed = Math.sqrt(gxRed * gxRed + gyRed * gyRed) >>> 0;
            const magnitudeGreen = Math.sqrt(gxGreen * gxGreen + gyGreen * gyGreen) >>> 0;
            const magnitudeBlue = Math.sqrt(gxBlue * gxBlue + gyBlue * gyBlue) >>> 0;

            // Set the Sobel data (clamping the values to 0-255)
            sobelData[pixelIndex] = magnitudeRed;
            sobelData[pixelIndex + 1] = magnitudeGreen;
            sobelData[pixelIndex + 2] = magnitudeBlue;
            sobelData[pixelIndex + 3] = 255; // Maintain full opacity
        }
    }

    // Copy the processed data back to the original data array
    for (let i = 0; i < data.length; i++) {
        data[i] = sobelData[i];
    }
}

  
  

