    ctx.putImageData(combinedImageData, 0, 0);
  
    // Convert the image to grayscale considering alpha
    const grayscaleData = new Uint8ClampedArray(width * height);
    for (let i = 0; i < combinedData.length; i += 4) {
      const r = combinedData[i];
      const g = combinedData[i + 1];
      const b = combinedData[i + 2];
      const alpha = combinedData[i + 3] / 255;
      const gray = (0.299 * r + 0.587 * g + 0.114 * b) * alpha;
      grayscaleData[i / 4] = gray;
    }
  
    // Apply Sobel operator to detect edges
    const sobel = (data, width, height) => {
      const kernelX = [
        [-1, 0, 1],
        [-2, 0, 2],
        [-1, 0, 1]
      ];
      const kernelY = [
        [-1, -2, -1],
        [0, 0, 0],
        [1, 2, 1]
      ];
      const sobelData = new Uint8ClampedArray(data.length);
      const offset = width;
  
      for (let y = 1; y < height - 1; y++) {
        for (let x = 1; x < width - 1; x++) {
          const idx = y * width + x;
          let sumX = 0;
          let sumY = 0;
  
          for (let ky = -1; ky <= 1; ky++) {
            for (let kx = -1; kx <= 1; kx++) {
              const kIdx = (y + ky) * width + (x + kx);
              sumX += data[kIdx] * kernelX[ky + 1][kx + 1];
              sumY += data[kIdx] * kernelY[ky + 1][kx + 1];
            }
          }
  
          const magnitude = Math.sqrt(sumX * sumX + sumY * sumY);
          sobelData[idx] = magnitude;
        }
      }
      return sobelData;
    };
  
    const sobelData = sobel(grayscaleData, width, height);
  
