import React, { useState, useEffect, useTransition } from 'react';
import styles from './Agents.module.css';

import { MediaDisplay, formatBytes, getFileExtension, ImageModal, fetchAndSetImage, fileDisplayAble } from "../../../utils/utils.jsx";
import Cookies from 'js-cookie';
import { Modal, Popup } from 'antd-mobile';

function ImageDisplayCombo({ file, watermark=false }) {
  const [imageUrl, setImageUrl] = useState('');
  const [showModal, setShowModal] = useState(false);
  const [isimage, setIsImage] = useState(true);

  useEffect(() => {
    if (fileDisplayAble(file)) {
      fetchAndSetImage(file).then(fileUrl => setImageUrl(fileUrl)).catch(console.error);
    }else{
      setIsImage(false)
    }

    return () => imageUrl && URL.revokeObjectURL(imageUrl);
  }, [file.id, file.type, file.name]);

  const handleMediaClick = (e) => {
    e.preventDefault(); // Prevent default behavior (downloading the image)
    setShowModal(true); // Show modal window on media click
    document.body.style.cursor = 'zoom-out'; // Change cursor to magnifier
  };

  const closeModal = () => {
    setShowModal(false); // Close modal window
    document.body.style.cursor = 'pointer'; // Change cursor to magnifier
  };

  return (
    <div style={{  maxWidth: '100%', height: 'auto' }} >
      {imageUrl ? (
       
          <MediaDisplay
            url={imageUrl}
            type={file.type}
            muted={true}
            controls={true}
            autoplay={true}
            watermark={watermark}
            onMediaClick={handleMediaClick} // Pass click handler to MediaDisplay component            
          />
      ) : (
        <>
        {isimage ?(<div>加载中...</div>):
          (<div>点击下载...</div>)
        }
        </>
      )
      }



<Popup
        visible={showModal}
        position='bottom'
        style={{ zIndex: 1000000}} // Apply directly if bodyStyle doesn't work
        bodyStyle={{ width: '100%', height: '100%' }} // Dynamic height
        destroyOnClose={true}
        closeOnMaskClick={true}
        showCloseButton
    >

      <ImageModal closeModal={closeModal} imageUrl={imageUrl} filetype={file.type}></ImageModal>
</Popup>
      
    </div>
  );
}

export default function Files({ files, downloadFile, returnResourceIcon, shareContent=null }) {

  const userType = Cookies.get('userType')
  return (
    <div className='gridContainer_resouce_new'> {/* Use the grid container class here */}
      {files && files.filter(f=>(!f.name.endsWith('.npy')&&!f.name.endsWith('.json'))).map((file) => (
        <div key={file.id}> {/* This div wraps both file details and image */}
          <div className={styles.history_box}> {/* Adjusted width to 100% */}
                <ImageDisplayCombo file={file} watermark={userType == 'guest'}/>
            <div  
            style={{display: 'flex', cursor:'pointer'}}>
                {/*<img style={{width:'28px', height:'46px'}} src={returnResourceIcon(file)} alt="file-icon" style={{borderRadius: "8px"}}/>*/}
              <div  className={styles.picListBot}>
                <div style={{fontSize: '10px',color:'#787878'}} className={styles.single_line_block} onClick={() => downloadFile(file.id, file.name)} >{file.name}</div>

                <div style={{fontSize: '9px',textAlign:'right'}} onClick={() => downloadFile(file.id, file.name)} className={styles.downBth}>
                  {file.type.split("/")[1]}{file.size !== '' ? ` • ${formatBytes(file.size)}` : ''}
                  <span>点击下载{(userType == 'user' || userType == 'guest')?'(含水印)':''}</span>
                </div>
                { (userType == 'user' || userType == 'guest') &&
                <div style={{fontSize: '9px',textAlign:'right'}} onClick={() => downloadFile(file.id, file.name)} className={styles.downBth}>
                  {file.type.split("/")[1]}{file.size !== '' ? ` • ${formatBytes(file.size)}` : ''}
                  <span>注册去水印下载</span>
                </div>
                }
                {shareContent &&
                <div style={{fontSize: '9px', padding:'5px'}}>
                  <button onClick={() => shareContent(file.id)} style={{background:'#4591ee',width:'100%',border:'none',color:'#fff',borderRadius:'8px',padding:'7px 25px',fontSize:'12px'}}>发布</button>
                </div>
                }
              </div>
            </div>
          </div>

        </div>
      ))}
    </div>
  );
}

