import React, { useState, useRef, useEffect } from 'react';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { getResources, getAgentRunResources, uploadFile } from "../../api/DashboardService.jsx";
import ResourceList from "../../Content/Agents/ResourceList.jsx";

export default function ResourceManager({ agentId, runs, runid, selectedRunStatus }) {
  const [output, setOutput] = useState([]);
  const [input, setInput] = useState([]);
  const [channel, setChannel] = useState('input')
  const [isDragging, setIsDragging] = useState(false);
  const fileInputRef = useRef(null);

  useEffect(() => {
    const interval = window.setInterval(function () {
      if (selectedRunStatus !== "ERROR_PAUSED" && selectedRunStatus !== "COMPLETED" && selectedRunStatus !== "TERMINATED" && selectedRunStatus !== "ITERATION_LIMIT_EXCEEDED") {
        fetchResources();
      }
    }, 5000);

    return () => clearInterval(interval);
  }, [runid, selectedRunStatus]);


  function handleFile(files) {
    if (files.length > 0) {
      const sizeInMB = files[0].size / (1024 * 1024);
      if (sizeInMB > 5) {
        toast.error('File size should not exceed 5MB', { autoClose: 1800 });
      } else {
        const fileData = {
          "file": files[0],
          "name": files[0].name,
          "size": files[0].size,
          "type": files[0].type,
        };
        uploadResource(fileData);
      }
    }
  };
  const handleFileInputChange = (event) => {
    const files = event.target.files;
    handleFile(files);
  };

  const handleDropAreaClick = () => {
    fileInputRef.current.click();
  };

  const handleDragEnter = (event) => {
    event.preventDefault();
    setIsDragging(true);
  };

  const handleDragLeave = () => {
    setIsDragging(false);
  };

  const handleDragOver = (event) => {
    event.preventDefault();
  };

  const handleDrop = (event) => {
    event.preventDefault();
    setIsDragging(false);
    const files = event.dataTransfer.files;
    handleFile(files);
  };

  useEffect(() => {
    fetchResources();
  }, [agentId]);

  function uploadResource(fileData) {
    const formData = new FormData();
    formData.append('file', fileData.file);
    formData.append('name', fileData.name);
    formData.append('size', fileData.size);
    formData.append('type', fileData.type);

    uploadFile(agentId, formData)
      .then((response) => {
        fetchResources();
        toast.success('Resource added successfully', { autoClose: 1800 });
      })
      .catch((error) => {
        toast.error(error, { autoClose: 1800 });
        console.error('Error uploading resource:', error);
      });
  }

  function fetchResources() {
    getResources(agentId)
      .then((response) => {
        const resources = response.data;
        const inputFiles = resources.filter((resource) => resource.channel === 'INPUT');
        const outputFiles = resources.filter((resource) => resource.channel === 'OUTPUT');
        setInput(inputFiles);
        setOutput(outputFiles);
      })
      .catch((error) => {
        console.error('Error fetching resources:', error);
      });
  }

  return (
    <div className='sectionCon'>
      <div className='section-area'>
        <div className="detail_top mb_10">
          <div className='documentTitle'>
            <i></i>
            <span>运行结果</span>
          </div>
          {/*<button onClick={() => setChannel('output')}*/}
          {/*        className={channel === 'output' ? 'tab_button_selected' : 'tab_button'}>输出文件111*/}
          {/*</button>*/}
        </div>
        <div className="w_100 mr_10 mb_20">
          <ResourceList files={output} channel={'output'} runs={runs} runid={runid} selectedRunStatus={selectedRunStatus} />
        </div>
      </div>

      <div className='section-area'>
        <div className="detail_top mb_10 width=100%">
          <div className='documentTitle'>
            <i></i>
            <span>原始文件</span>
          </div>
        </div>
        <div className="w_100 mr_10 mb_20">
          <ResourceList files={input} channel={'input'} runs={runs} selectedRunStatus={selectedRunStatus} />
        </div>
      </div>
      <ToastContainer />
    </div>)
}