import React, { useRef, useEffect, useState } from 'react';

import { v4 as uuidv4 } from 'uuid';
function ImageEditor({param, canvasWidth, canvasHeight, url, handleChanged}) {
    const canvasRef = useRef(null);
    const [imageInfo, setImageInfo] = useState({ x: 0, y: 0, width: 0, height: 0, scale: 1 });
    const [imageLoaded, setImageLoaded] = useState(false);
    const [rect, setRect] = useState({ x: 0, y: 0, width: 0, height: 0 });
    const [dragging, setDragging] = useState(false);
    const [dragStart, setDragStart] = useState(null);
    const [draggingImage, setDraggingImage] = useState(false);
    const [image, setImage] = useState(null);

    const [ratio, setRatio] = useState(1)
    const fileInputRef = useRef(null);



    const [initialImageInfo, setInitialImageInfo] = useState(null);

    
    const handleEditted = (file) => {
        if (file) {
            /*
            if (file.name.length > 30) {
                alert('文件名称过长，请上传文件名不超过30个字符的文件。');
                return; // Stop further execution if the file name is too long
            }*/
            const validExtensions = param.accept.split(',');
            const fileExtension = file.name.split('.').pop().toLowerCase();
            if (validExtensions=="" || validExtensions.includes(`.${fileExtension}`)) {
                const syntheticEvent = {
                    target: {
                        name: param.name,
                        files: [file],
                    },
                };
                handleChanged(syntheticEvent);
            } else {
                alert('文件类型暂不支持，请上传如下类型文件：'+param.accept);
            }
        }
    };

    const handleImageChange = (e) => {
        e.preventDefault();
        e.stopPropagation(); // Stop the event from propagating to parent elements or the browser
        const file = e.target.files[0];
        if (file) {
          handleChanged(e); 
        }
      };

    useEffect(() => {
        const image = new Image();
        //image.src = '/images/Bella.jpeg'; // Replace with your image path
        //image.src = '/images/liuyifei.png'; // Replace with your image path
        image.src = url; // Replace with your image path
        image.onload = () => {

            const maxScaleWidth = (canvasWidth/1.5) / image.width; // Assuming the canvas width is canvasWidthpx
            const maxScaleHeight = (canvasHeight/1.5) / image.height; // Assuming the canvas height is canvasHeightpx
           
            const scale =  Math.min(maxScaleHeight,maxScaleWidth);
            const newImageInfo = {
                image: image,
                x: (canvasRef.current.width - image.width * scale) / 2,
                y: (canvasRef.current.height - image.height * scale) / 2,
                width: image.width * scale,
                height: image.height * scale,
                scale: scale
            };
            setImageInfo(newImageInfo);
            setRect({ x: newImageInfo.x, y: newImageInfo.y, width: newImageInfo.width, height: newImageInfo.height });
            setInitialImageInfo(newImageInfo); // Store initial image info for reset
            setRatio(scale)
            setImageLoaded(true);
            setImage(image);
        };
    }, [canvasWidth, canvasHeight, url]);

    const resetImage = (e) => {
        e.preventDefault()
        if (initialImageInfo) {
            setImageInfo(initialImageInfo);
            setRect({ x: initialImageInfo.x, y: initialImageInfo.y, width: initialImageInfo.width, height: initialImageInfo.height });
        }
    };
    

    const drawCheckeredPattern = (context, canvas) => {
        const squareSize = 15; // Size of the checkered squares
        const numRows = canvas.height / squareSize;
        const numCols = canvas.width / squareSize;
        for (let i = 0; i < numRows; i++) {
            for (let j = 0; j < numCols; j++) {
                context.fillStyle = (i + j) % 2 === 0 ? '#FFF' : '#DDD'; // Alternate colors
                context.fillRect(j * squareSize, i * squareSize, squareSize, squareSize);
            }
        }
    };
    

    const drawMaxDimensions = (context) => {
        const maxScaleWidth = canvasWidth / image.width; // Assuming the canvas width is canvasWidthpx
        const maxScaleHeight = canvasHeight / image.height; // Assuming the canvas height is canvasHeightpx
    
        const maxWidth = image.width * maxScaleWidth;
        const maxHeight = image.height * maxScaleHeight;
    
        context.font = '16px Arial';
        context.fillStyle = 'black';
        context.textAlign = 'right';
        context.font = '12px Arial'; // Reduced font size by 2 points from 16px to 14px
        context.fillText(`最大宽度: ${Math.round(maxWidth/ratio)}px, 最大高度: ${Math.round(maxHeight/ratio)}px`, canvasRef.current.width - 10, canvasRef.current.height - 10);
    };
    
    useEffect(() => {
        const canvas = canvasRef.current;
        const context = canvas.getContext('2d');
        canvas.width = canvasWidth;
        canvas.height = canvasHeight;

        drawCheckeredPattern(context,canvas);
    }, []);



    const handleMouseWheel = (e) => {
        e.preventDefault();
        e.stopPropagation(); // Stop the event from propagating to parent elements or the browser
        
        // 缩放的速度因子，可以根据需要调整更细或更粗的缩放
        const baseScaleFactor = 0.01; // 基础缩放因子
        const scaleFactor = baseScaleFactor * (1024 / image.width); // 假设500是参考宽度

        // 获取当前鼠标位置相对于画布的坐标
        const mouseX = e.clientX - canvasRef.current.getBoundingClientRect().left;
        const mouseY = e.clientY - canvasRef.current.getBoundingClientRect().top;
    
        // 根据滚轮方向调整缩放比例
        const deltaScale = e.deltaY > 0 ? -scaleFactor : scaleFactor;
        setImageInfo((prevInfo) => {
            // 计算新的缩放比例，确保它在最小和最大值之间
            const newScale = Math.max(0.02, Math.min(8, prevInfo.scale + deltaScale));  // 设置最小缩放为0.1，最大缩放为3
    
            // 根据缩放中心进行调整
            const newWidth = image.width * newScale;
            const newHeight = image.height * newScale;
            const newX = mouseX - (mouseX - prevInfo.x) * (newScale / prevInfo.scale);
            const newY = mouseY - (mouseY - prevInfo.y) * (newScale / prevInfo.scale);
    
            return {
                ...prevInfo,
                x: newX,
                y: newY,
                width: newWidth,
                height: newHeight,
                scale: newScale
            };
        });
    };
    
    
    

    const handleMouseDown = (e) => {
        e.preventDefault()
        const rectStart = {
            x: e.clientX - canvasRef.current.getBoundingClientRect().left,
            y: e.clientY - canvasRef.current.getBoundingClientRect().top,
        };
    
        // Check if the click is on a rectangle corner first
        const cornerSize = 8;
        if (
            Math.abs(rectStart.x - rect.x) < cornerSize &&
            Math.abs(rectStart.y - rect.y) < cornerSize
        ) {
            setDragging(true);
            setDragStart({ x: rectStart.x - rect.x, y: rectStart.y - rect.y, corner: 'topLeft' });
            return;
        } else if (
            Math.abs(rectStart.x - (rect.x + rect.width)) < cornerSize &&
            Math.abs(rectStart.y - rect.y) < cornerSize
        ) {
            setDragging(true);
            setDragStart({ x: rectStart.x - (rect.x + rect.width), y: rectStart.y - rect.y, corner: 'topRight' });
            return;
        } else if (
            Math.abs(rectStart.x - rect.x) < cornerSize &&
            Math.abs(rectStart.y - (rect.y + rect.height)) < cornerSize
        ) {
            setDragging(true);
            setDragStart({ x: rectStart.x - rect.x, y: rectStart.y - (rect.y + rect.height), corner: 'bottomLeft' });
            return;
        } else if (
            Math.abs(rectStart.x - (rect.x + rect.width)) < cornerSize &&
            Math.abs(rectStart.y - (rect.y + rect.height)) < cornerSize
        ) {
            setDragging(true);
            setDragStart({ x: rectStart.x - (rect.x + rect.width), y: rectStart.y - (rect.y + rect.height), corner: 'bottomRight' });
            return;
        }
    
        // If not clicking on a corner, initiate dragging the image
        setDraggingImage(true);
        setDragStart({ x: rectStart.x - imageInfo.x, y: rectStart.y - imageInfo.y });
    };
    

    const handleMouseMove = (e) => {
        e.preventDefault()
        if (draggingImage) {
            const mouseX = e.clientX - canvasRef.current.getBoundingClientRect().left;
            const mouseY = e.clientY - canvasRef.current.getBoundingClientRect().top;

            setImageInfo(prevInfo => ({
                ...prevInfo,
                x: mouseX - dragStart.x,
                y: mouseY - dragStart.y
            }));
            return;
        }

        if (!dragging) return;

        const mouseX = e.clientX - canvasRef.current.getBoundingClientRect().left;
        const mouseY = e.clientY - canvasRef.current.getBoundingClientRect().top;

        const newX = mouseX + dragStart.x;
        const newY = mouseY + dragStart.y;

        setRect((prevRect) => {
            switch (dragStart.corner) {
                case 'topLeft':
                    return { ...prevRect, x: newX, y: newY, width: prevRect.width + (prevRect.x - newX), height: prevRect.height + (prevRect.y - newY) };
                case 'topRight':
                    return { ...prevRect, y: newY, width: newX - prevRect.x, height: prevRect.height + (prevRect.y - newY) };
                case 'bottomLeft':
                    return { ...prevRect, x: newX, width: prevRect.width + (prevRect.x - newX), height: newY - prevRect.y };
                case 'bottomRight':
                    return { ...prevRect, width: newX - prevRect.x, height: newY - prevRect.y };
                default:
                    return prevRect;
            }
        });
    };

    const handleMouseUp = () => {
        setDragging(false);
        setDraggingImage(false);
    };

    useEffect(() => {
        if (imageLoaded && imageInfo) {
            const canvas = canvasRef.current;
            const context = canvas.getContext('2d');
            context.clearRect(0, 0, canvas.width, canvas.height);
            drawCheckeredPattern(context, canvas);  // Redraw the background pattern first
            context.drawImage(imageInfo.image, imageInfo.x, imageInfo.y, imageInfo.width, imageInfo.height);
            drawRect(context);  // Draw or redraw the rectangle
            drawMaxDimensions(context); // Draw the max dimensions text
        }
    }, [imageInfo, imageLoaded, rect]);

    const drawRect = (context) => {
        const canvas = canvasRef.current;
        context.clearRect(0, 0, canvas.width, canvas.height);
        drawCheckeredPattern(context, canvas);  // Redraw the background first
        context.drawImage(image, imageInfo.x, imageInfo.y, imageInfo.width, imageInfo.height);
    
        // Draw the rectangle
        context.strokeStyle = 'grey';
        context.lineWidth = 2;
        context.strokeRect(rect.x, rect.y, rect.width, rect.height);
    
        // Draw the corner squares
        const cornerSize = 8;
        context.fillStyle = 'blue';
        context.fillRect(rect.x - cornerSize / 2, rect.y - cornerSize / 2, cornerSize, cornerSize); // Top-left
        context.fillRect(rect.x + rect.width - cornerSize / 2, rect.y - cornerSize / 2, cornerSize, cornerSize); // Top-right
        context.fillRect(rect.x - cornerSize / 2, rect.y + rect.height - cornerSize / 2, cornerSize, cornerSize); // Bottom-left
        context.fillRect(rect.x + rect.width - cornerSize / 2, rect.y + rect.height - cornerSize / 2, cornerSize, cornerSize); // Bottom-right
    };

    function createScaledImage(image, scale) {
        const tempCanvas = document.createElement('canvas');
        const tempCtx = tempCanvas.getContext('2d');
    
        // 设置临时 canvas 的大小为图像的缩放大小
        tempCanvas.width = image.width / scale;
        tempCanvas.height = image.height / scale;
    
        // 在临时 canvas 上绘制缩放后的图像
        tempCtx.drawImage(image, 0, 0, tempCanvas.width, tempCanvas.height);
    
        return tempCanvas; // 返回一个包含缩放图像的 canvas
    }
    
    
    const saveImage=(e)=>{

        e.preventDefault();
        const offscreenCanvas = document.createElement('canvas');
        const ctx = offscreenCanvas.getContext('2d');
    
        // 设置新 canvas 的大小
        offscreenCanvas.width = Math.round(rect.width / ratio);
        offscreenCanvas.height = Math.round(rect.height / ratio);
    
        // 计算原始图像相对于当前图像的缩放比例
        const scale = initialImageInfo.width / imageInfo.width;
    
        // 使用 createScaledImage 函数来获取缩放后的图像
        const scaledCanvas = createScaledImage(initialImageInfo.image, scale);
        const scaledWidth = scaledCanvas.width;
        const scaledHeight = scaledCanvas.height;
    
        // 计算在新 canvas 上绘制图像的位置
        const destX = (imageInfo.x - rect.x) / ratio;
        const destY = (imageInfo.y - rect.y) / ratio;
    
        // 在新 canvas 上绘制缩放后的图像
        ctx.drawImage(scaledCanvas, 0, 0, scaledWidth, scaledHeight, destX, destY, scaledWidth, scaledHeight);
    
        // 将 canvas 转换为 Blob 并提供下载
        offscreenCanvas.toBlob((blob) => {
            const url = URL.createObjectURL(blob);
            const link = document.createElement('a');

            //const fileName = generateUniqueFilename('edited_.png');
            //const file = new File([blob], fileName, { type: 'image/png' });
            //handleEditted(file)
            
            link.href = url;
            link.download = 'saved-image.png';
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            URL.revokeObjectURL(url);
        }, 'image/png');
    }

    const confirmImage=(e)=>{

        e.preventDefault();
        const offscreenCanvas = document.createElement('canvas');
        const ctx = offscreenCanvas.getContext('2d');
    
        // 设置新 canvas 的大小
        offscreenCanvas.width = Math.round(rect.width / ratio);
        offscreenCanvas.height = Math.round(rect.height / ratio);
    
        // 计算原始图像相对于当前图像的缩放比例
        const scale = initialImageInfo.width / imageInfo.width;
    
        // 使用 createScaledImage 函数来获取缩放后的图像
        const scaledCanvas = createScaledImage(initialImageInfo.image, scale);
        const scaledWidth = scaledCanvas.width;
        const scaledHeight = scaledCanvas.height;
    
        // 计算在新 canvas 上绘制图像的位置
        const destX = (imageInfo.x - rect.x) / ratio;
        const destY = (imageInfo.y - rect.y) / ratio;
    
        // 在新 canvas 上绘制缩放后的图像
        ctx.drawImage(scaledCanvas, 0, 0, scaledWidth, scaledHeight, destX, destY, scaledWidth, scaledHeight);
    
        // 将 canvas 转换为 Blob 并提供下载
        offscreenCanvas.toBlob((blob) => {
            const url = URL.createObjectURL(blob);
            const link = document.createElement('a');

            const fileName = generateUniqueFilename('edited_.png');
            const file = new File([blob], fileName, { type: 'image/png' });
            handleEditted(file)
            
        }, 'image/png');
    }

    function generateUniqueFilename(path) {
        // Get the current datetime and format it as a string '20240503_123456'
        const now = new Date();
        const dateStr = now.getFullYear() +
                        ('0' + (now.getMonth() + 1)).slice(-2) +
                        ('0' + now.getDate()).slice(-2) + '_' +
                        ('0' + now.getHours()).slice(-2) +
                        ('0' + now.getMinutes()).slice(-2) +
                        ('0' + now.getSeconds()).slice(-2);
    
        // Generate a random UUID and convert it to a string
        const randomUuid = uuidv4().replace(/-/g, ''); // Removes dashes to match Python's uuid.hex
        
        // Create the unique prefix combining the date and UUID
        const uniquePrefix = `${dateStr}_${randomUuid}_`;
    
        // Extract the base name of the file from the path
        const baseName = path.split('/').pop(); // Assumes a '/' path delimiter
    
        // Prepend the unique prefix to the base name
        const newFilename = `${uniquePrefix}${baseName}`;
    
        return newFilename;
    }

    return (
        <div
            style={{position: 'relative', width: 'canvasWidthpx', height: 'canvasHeightpx', border: '1px solid black'}}>
            <canvas ref={canvasRef} onWheel={handleMouseWheel} onMouseDown={handleMouseDown}
                    onMouseMove={handleMouseMove} onMouseUp={handleMouseUp}></canvas>

            <div style={{position: 'absolute', top: '5px', left: '0px', fontSize: '10px', color: '#787878'}}>
                <span style={{
                    display: 'inline-block',
                    padding: '0 10px'
                }}>拖动四个角改变结果图大小，滚轮缩放原始图像，拖动图像改变位置，</span>
                <div style={{fontSize: '10px', padding: '0 10px'}}>
                    宽: {Math.round(rect.width / ratio)}px, 高: {Math.round(rect.height / ratio)}px；
                    原图在新图中坐标:（ {Math.round((imageInfo.x - rect.x) / ratio)}, {Math.round((imageInfo.y - rect.y) / ratio)} ）
                </div>
                <input
                    type="file"
                    ref={fileInputRef}
                    style={{display: 'none'}}
                    onChange={(e) => {
                        if (false) {
                            alert("请稍等加载中")
                        } else {
                            handleImageChange(e)
                        }
                    }}
                />

            </div>
            <div className='newfileInput'>
                <button onClick={(e) => {
                    e.preventDefault()
                    fileInputRef.current.click()
                }}>新建上传
                </button>
            </div>

            <div className='newfileInputCon'>
                <button onClick={(e) => resetImage(e)} style={{color: '#2C5ECE'}}>复原</button>
                <button onClick={(e) => saveImage(e)} style={{color: '#31BE4B'}}>下载</button>
                <button onClick={(e) => confirmImage(e)} style={{color: '#787878'}}>确认</button>
            </div>

        </div>
    );
}

export default ImageEditor;
