import React, { useState } from 'react';
import { useRef } from 'react';
import ImageEditor from "../../../utils/components/imageeditor";


// Inside your functional component
function EditableImage({ index, param, handleInputChange, previewUrl }) {
  const [isDragging, setIsDragging] = useState(false);

  const fileInputRef = useRef(null);

  const handleDragEnter = (e) => {
    e.preventDefault();
    setIsDragging(true);
  };

  const handleDragLeave = (e) => {
    e.preventDefault();
    setIsDragging(false);
  };

  const handleDragOver = (e) => {
    e.preventDefault();
  };


  const handleDrop = (e) => {
    e.preventDefault();
    setIsDragging(false);
    const file = e.dataTransfer.files[0];
    if (file) {
      /*
      if (file.name.length > 30) {
        alert('文件名称过长，请上传文件名不超过30个字符的文件。');
        return; // Stop further execution if the file name is too long
      }*/
      const validExtensions = param.accept.split(',');
      const fileExtension = file.name.split('.').pop().toLowerCase();
      if (validExtensions=="" || validExtensions.includes(`.${fileExtension}`)) {
          const syntheticEvent = {
              target: {
                  name: param.name,
                  files: [file],
              },
          };
          handleInputChange(index, syntheticEvent);
      } else {
          alert('文件类型暂不支持，请上传如下类型文件：'+param.accept);
      }
    }
  };

  const handleInputChangeWithIndex = (index) => async (newValue) => {
    await handleInputChange(index, newValue);
  };

  const handleImageChange = (e) => {
    e.preventDefault();
    const file = e.target.files[0];
    if (file) {
      /*
      if (file.name.length > 30) {
        alert('文件名称过长，请上传文件名不超过30个字符的文件。');
        return; // Stop further execution if the file name is too long
      }*/
      handleInputChange(index, e); 
    }
  };

  const handleCancel = () => {
    handleInputChange(index, null); 
  };

  return (
    <div className={`my-file-drop-area ${isDragging ? 'dragging' : ''}`}
        onDragEnter={handleDragEnter}
        onDragLeave={handleDragLeave}
        onDragOver={handleDragOver}
        onDrop={handleDrop}
        onClick={() => {
          if (!previewUrl){
            fileInputRef.current.click()}
          }

        } // Trigger file input click on area click
        style={{ position: 'relative', minHeight:window.innerWidth>1000?'500px':'320px', maxWidth: '100%', maxHeight: '100%' }} // Ensure the container resizes with the image
    >
    <input
        type="file"
        ref={fileInputRef}
        style={{ display: 'none' }}
        name={param.name}
        accept={param.accept}
        onChange={(e) => handleImageChange(e)}
    />
    {previewUrl ? (
        //<div className="preview" style={{ position: 'relative', maxWidth: '100%', maxHeight: '100%' }}>
        <>
        <ImageEditor param={param} canvasHeight={window.innerWidth>1000?480:300} canvasWidth={window.innerWidth>1000?320:200} url={previewUrl} handleChanged={handleInputChangeWithIndex(index)}/>
        
        <button
            type="button" // Specify button type as "button"
            className="cancel"
            onClick={(e) => {
                e.stopPropagation(); // Prevent click event from bubbling up to the file drop area
                fileInputRef.current.value = '';
                handleInputChange(index, null); // Call the close handler
            }}
            style={{
                position: 'absolute',
                top: '10px',
                right: '10px',
                padding: '2px',
                fontSize: '10px',
                borderRadius: '5%',
                backgroundColor: '#ccc',
                border: 'none',
                cursor: 'pointer',
            }}
        >
            X
        </button>
        </>
    ) : (
        <p style={{ textAlign: 'center', margin: 0 , fontSize: '13px'}}>+</p>
    )}
    </div>


  );
}

export default EditableImage;
