import axios from 'axios';
import Cookies from "js-cookie";

let env =null
try{
  if (process == undefined || process.env== undefined){
    env = import.meta
  }else{
    env = process.env
  }
}catch(e){
  env = {'VITE_API_BASE_URL':'http://localhost/api/', 
    'VITE_WEB_SITE':'/appstore_panel/dist', 
    'VITE_MARKETPLACE_SITE':'http://localhost', 
    "VITE_APP_BASE_URL":'/appstore_panel/dist'
  }
}


const GITHUB_CLIENT_ID = env.GITHUB_CLIENT_ID;
export const API_BASE_URL = env.VITE_API_BASE_URL || 'https://huaxiaobao.net/api/';


export const WEB_SITE = env.VITE_WEB_SITE || 'https://huaxiaobao.net'
export const MARKETPLACE_SITE = env.VITE_MARKETPLACE_SITE || 'https://huaxiaobao.net'

export const MARKET_API_BASE_URL = MARKETPLACE_SITE+'/api';

const GOOGLE_ANALYTICS_MEASUREMENT_ID =  env.GOOGLE_ANALYTICS_MEASUREMENT_ID;
const GOOGLE_ANALYTICS_API_SECRET =  env.GOOGLE_ANALYTICS_API_SECRET;
const MIXPANEL_AUTH_ID = env.NEXT_PUBLIC_MIXPANEL_AUTH_ID

 
export const baseUrl = () => {
  return API_BASE_URL;
};

export const baseWebsite = () => {
  if ( window.location.hostname.includes('shangxiaobao')){
    return '.huaxiaobao.net'
  }else if (window.location.hostname.includes('taoai')) {
    return '.taoai.store'
  }else if (window.location.hostname.includes('aiugc')) {
    return '.aiugc.org'
  }else if (window.location.hostname.includes('dianxiaoshang')) {
    return '.dianxiaoshang.com'
  }else{
    return window.location.hostname;
  }
}; 

export const githubClientId = () => {
  return GITHUB_CLIENT_ID;
};

export const analyticsMeasurementId = () => {
  return GOOGLE_ANALYTICS_MEASUREMENT_ID;
};

export const analyticsApiSecret = () => {
  return GOOGLE_ANALYTICS_API_SECRET;
};

export const mixpanelId = () => {
  return MIXPANEL_AUTH_ID;
};

const api = axios.create({
  baseURL: API_BASE_URL,
  headers: {
    common: {
      'Content-Type': 'application/json',
    },
  },
  proxy: {
    host: 'http://www.huaxiaobao.net',
    port: 8081
  }
});


const market_api = axios.create({
  baseURL: 
  MARKET_API_BASE_URL,
  headers: {
    common: {
      'Content-Type': 'application/json',
    },
  },
});

api.interceptors.request.use(config => {
  if (typeof window !== 'undefined') {
    // const accessToken = localStorage.getItem("accessToken");
    let accessToken = Cookies.get("accessToken");
    if (!accessToken){
      accessToken= localStorage.getItem('accessTokenHuaxiaobao')
    }
    console.log(accessToken, 'we have the this token')
    if (accessToken!=undefined && accessToken!='') {
      config.headers['Authorization'] = `Bearer ${accessToken}`;
    }
  }
  return config;
});

export default api;
